import {Card, CardContent, CardMedia, Grid} from "@material-ui/core";
import React from "react";
import '../../styles/text.css';
import '../../styles/card.css';

/**
 * Renders experience card for displaying work experience.
 * Parameters: props.children props.subtitle props.title props.link props.img
 */
export default function ExperienceCard(props) {
    return(
        <Grid item xs={12} sm={12} md={8} lg={6}>
            <Card shadow={2}>
                <CardMedia
                    component="img"
                    src={props.img}
                    className={'experience-card-img'}
                />
                <CardContent className={'experience-card'}>
                    <div className={'card-text'}>
                        <div className={'subtitle'}><a href={props.link}>{props.title}</a></div>
                        <div className={'position-text'}>{props.subtitle}</div>
                        {props.children}
                    </div>
                </CardContent>
            </Card>
        </Grid>
    )
}