import React from 'react';
import '../../styles/layout.css';
import '../../styles/assets.css';
import '../../styles/text.css';
import pfp from '../../assets/icons/pfp.svg';
import Navbar from './navbar';

/**
 * Renders the Header, with the logo changing size depending on window size.
 */
export default class Header extends React.Component {

    constructor(props){
        super(props)
        this.state = {
            width: window.innerWidth,
            height: window.innerHeight
        }
    }

    /**
     * Updates the width and height states.
     */
    resize() {
        this.setState({
            width: window.innerWidth,
            height: window.innerHeight
        })
    }

    /**
     * Adds the event listener that checks for window resizes.
     */
    componentDidMount() {
        window.addEventListener("resize", this.resize.bind(this))
    }

    /**
     * Removes the event listener that checks for window resizes.
     */
    componentWillUnmount(){
        window.removeEventListener("resize", this.resize.bind(this))
    }

    /**
     * Renders the header, with styling changes depending on if it's in mobile or desktop.
     */
    render(){

        // change styles based on mobile or desktop layout
        let profile_styles = {}
        if (this.state.width > 700 && this.state.height > 700){
            profile_styles.width = '400px'
            profile_styles.height = '400px'
        } else{
            profile_styles.width = '200px'
            profile_styles.height = '200px'
            profile_styles.marginTop = '10vh'
        }

        return(
            <div>
                <Navbar />
                <div className={'header'}>
                    <div className={'profile'}>
                        <img src={pfp} alt={"pfp.svg"} style={profile_styles} />
                        <h1>Jan Garong</h1>
                        <h2>Software Developer</h2>
                    </div>
                </div>
            </div>
        )
    }
}