import {Card, CardContent, CardMedia, CardActions, Grid, Button, CardHeader} from "@material-ui/core";
import React from "react";
import '../../styles/text.css';
import '../../styles/card.css';

/**
 * Renders project card for displaying projects.
 * Parameters: props.children props.subtitle props.title props.link props.img
 */
export default function ProjectCard(props) {

    const styleButton = {
        color: 'white',
        margin: '20px 20px 50px 20px',
        fontSize: '14pt',
    }

    const styleSubtitle = {
        color: 'lightgrey'
    }

    return(
        <Grid item xs={12} sm={12} md={8} lg={6}>
            <Card shadow={2}>
                <CardHeader
                title={props.title}
                subheader={
                    <div style={styleSubtitle}>
                        {props.subtitle}
                    </div>
                }
                className={'project-card-header'}
                />
                <CardMedia
                    component="img"
                    src={props.img}
                    className={'experience-card-img'}
                />
                <CardContent className={'project-card'}>
                    <div className={'card-text'}>
                        {props.children}
                    </div>
                </CardContent>
                <CardActions className={'project-card-action'}>
                    <Button size="small" style={styleButton} href={props.link}>See Project</Button>
                </CardActions>
            </Card>
        </Grid>
    )
}