import React from 'react';
import Chip from '@material-ui/core/Chip';

/**
 * Renders Python Chip
 */
export function PythonSkill() {
    const style = {color: '#FFFFFF',
                   backgroundColor: '#3572A5'}

    const outerStyle = {
        marginRight: '5px',
        marginLeft: '5px',
    }

    return(
        <div style={outerStyle} >
        <Chip size={"small"} label={"Python"} style={style} />
        </div>
    )
}

