/**
 * Renders all skills available.
 */
export default function Skills() {
    const style = {
        display: 'flex',
        flexWrap: 'wrap',
    }

    return(
        <div style={style}>
            <b>Skills:</b>
            <PythonSkill />
            <JavaSkill />
            <CSkill />
            <JavascriptSkill />
            <PostgresqlSkill/>
            <ActionscriptSkill />
            <SoliditySkill />
            <NosqlSkill />
        </div>
    )
}