import React from "react"
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import WorkIcon from '@material-ui/icons/Work';
import GitIcon from '../../assets/icons/gitwhite.png';
import PersonIcon from '@material-ui/icons/Person';
import About from '../tabs/about';
import Experience from '../tabs/experience';
import Projects from '../tabs/projects';
import {createMuiTheme, MuiThemeProvider} from '@material-ui/core';

// example from: https://material-ui.com/components/tabs/

/**
 * Display tab if index and value matches
 */
function TabPanel(props) {
    const { children, value, index } = props;

    return (
        <div role="tabpanel" hidden={value !== index}>
            {value === index && (
                <div className={'tab-body'}>
                    {children}
                </div>
            )}
        </div>
    );
}

/**
 * Renders tabs and handles control of which tabs to select.
 */
const HomeTab = () => {

    // modify theme
    const theme = createMuiTheme({
        palette: {
            secondary: {
                main: '#f6c90e'
            },
        }
    });

    // create handlers for value change
    const [value, setValue] = React.useState(0);
    const handleChange = (event, newValue) => {
        setValue(newValue);
    };


    // set styles
    const iconStyle = {width:'36px', height: '36px'}
    const imgStyle = {width:'32px', height: '32px'}
    const buttonStyle = {paddingTop: '10px', paddingBottom: '10px'}
    const tabStyle = {color: 'white', backgroundColor: '#303841'}

    // render tabs
    return (
        <MuiThemeProvider theme={theme}>
            <AppBar position="static" style={tabStyle}>
                <Tabs
                    value={value}
                    onChange={handleChange}
                    centered
                    // #f6c90e
                >
                    <Tab label="About Me" icon={<PersonIcon style={iconStyle} />} style={buttonStyle} />
                    <Tab label="Experience" icon={<WorkIcon style={iconStyle} />} style={buttonStyle} />
                    <Tab label="Projects" icon={<img src={GitIcon} style={imgStyle} alt={'git'} />} style={buttonStyle} />
                </Tabs>
            </AppBar>
            <TabPanel value={value} index={0}>
                <About />
            </TabPanel>
            <TabPanel value={value} index={1}>
                <Experience />
            </TabPanel>
            <TabPanel value={value} index={2}>
                <Projects />
            </TabPanel>
        </MuiThemeProvider>
    );
}

export default HomeTab;
