import React from 'react';
import useScrollTrigger from '@material-ui/core/useScrollTrigger'
import '../../styles/layout.css';
import '../../styles/assets.css';
import GithubIcon from '@material-ui/icons/GitHub'
import LinkedinIcon from '@material-ui/icons/LinkedIn'
// import DescriptionIcon from '@material-ui/icons/Description';
import { Slide, AppBar, Toolbar, IconButton } from '@material-ui/core'
// import Resume from '../../assets/downloadables/resume.pdf'

/**
 * Renders the NavBar which contains buttons for GitHub, LinkedIn and Resume.
 * See https://material-ui.com/components/app-bar/ for more details.
 * Parameters: props.window props.children
 */
function HideOnScroll(props){
    const trigger = useScrollTrigger({ target: props.window ? props.window() : undefined})

    return (
        <Slide appear={false} direction="down" in={!trigger}>
            {props.children}
        </Slide>
    )
}

/**
 * Renders the NavBar which contains buttons for GitHub, LinkedIn and Resume.
 */
export default function Navbar(props){
    return(
        <HideOnScroll {...props}>
            <AppBar>
                <Toolbar className={'navbar'}>
                    Jan Garong
                    <div>
                        <IconButton color={'inherit'}
                                    href={'https://github.com/jangarong'}
                                    className={'icon-button'}>
                            <GithubIcon className={'material-icon'}/>
                        </IconButton>
                        <IconButton color={'inherit'}
                                    href={'https://www.linkedin.com/in/jangarong/'}
                                    className={'icon-button'}>
                            <LinkedinIcon className={'material-icon'} />
                        </IconButton>
                        {/* <IconButton color={'inherit'}
                                    className={'icon-button'}
                                    href={Resume} download>
                            <DescriptionIcon className={'material-icon'} />
                        </IconButton> */}
                    </div>
                </Toolbar>
            </AppBar>
        </HideOnScroll>
    )
}
